<template>
  <div class="group-message">
    <div class="title">
      <van-icon name="arrow-left" @click="$router.back()" />
      <h2>聊天信息 ({{ userMessage.length }})</h2>  
    </div>
    <div class="user">
      <div class="user-item" v-for="i in userMessage" :key="i.nickname">
        <div class="log"><img :src="i.pic" alt="" /></div>
        <p>{{ i.nickname }}</p>
      </div>
      <div class="add" @click="$router.push('/adduseringroup')">
        <van-icon name="plus" />
      </div>
      <div class="del" v-show=" ownerId == userId" @click="$router.push('/deleteuserfromgroup')"><van-icon name="minus" /></div>
    </div>

    <div class="group-dec">
      <div class="group-item">
        <div class="text">群聊名称</div>
        <div class="log">
          {{ groupName }}
          <van-icon
            @click="changeGroupName"
            name="arrow"
            color="rgb(107,107,107)"
          />
        </div>
      </div>
      <div class="group-item">
        <div class="text">群二维码</div>
        <div class="log">
          <van-icon name="arrow" color="rgb(107,107,107)" />
        </div>
      </div>
      <div class="group-item">
        <div class="anno-info">
          <div class="title">
            <div class="text">群公告</div>
            <div class="log" @click="changeGroupAnnouncement">
              <van-icon name="arrow" color="rgb(107,107,107)" />
            </div>
          </div>
          <p>{{ groupAnnouncement }}</p>
        </div>
      </div>
      <div class="group-item">
        <div class="text">群管理</div>
        <div class="log">
          <van-icon name="arrow" color="rgb(107,107,107)" />
        </div>
      </div>
      <div class="group-item">
        <div class="text">备注</div>
        <div class="log">
          <van-icon name="arrow" color="rgb(107,107,107)" />
        </div>
      </div>
      <div class="group-item">
        <div class="text">查找聊天记录</div>
        <div class="log">
          <van-icon name="arrow" color="rgb(107,107,107)" />
        </div>
      </div>
      <div class="group-item">
        <div class="text">消息免打扰</div>
        <div class="log">
          <van-switch
            v-model="checked1"
            size="20px"
            active-color="rgb(100, 242, 112)"
            inactive-color="rgb(107,107,107)"
          />
        </div>
      </div>
      <div class="group-item">
        <div class="text">置顶聊天</div>
        <div class="log">
          <van-switch
            v-model="checked2"
            size="20px"
            active-color="rgb(100, 242, 112)"
            inactive-color="rgb(107,107,107)"
          />
        </div>
      </div>
      <div class="group-item">
        <div class="text">保存到通信录</div>
        <div class="log">
          <van-switch
            v-model="checked3"
            size="20px"
            active-color="rgb(100, 242, 112)"
            inactive-color="rgb(107,107,107)"
          />
        </div>
      </div>
      <div class="group-item">
        <div class="text">我在群里的昵称</div>
        <div class="log">
          {{ userName }}
          {{ userMessage.pic }}
          <van-icon name="arrow" color="rgb(107,107,107)" />
        </div>
      </div>
      <p class="is-dismiss-group" v-if="isGroupOnwer" >
        <van-cell
          @click="isToSetDismissGroup"
          style="background: var(--theme-cell-black);margin-top:10px"
         
        >
          <button> 解散该群聊 </button></van-cell
        >
      </p>
     
    </div>
    <van-popup
      v-model="isDismissGroup"
      round
      class="dismiss-group-popup"
      :style="{ height: '15.2%', width: '70%' }"
      get-container=".group-message"
    >
      <p>是否解散群聊</p>
      <p>
        <button @click="cencelDisMissGroup">取消</button>
        <button @click="disMissGroup">解散</button>
      </p>
    </van-popup>
    <div class="bow-out" v-show=" ownerId !== userId" @click="bowOut()">
              退出群聊
         </div>
    <van-popup
      v-model="isRouterWriterGroupAnno"
      round
      get-container=".group-message"
      :style="{ height: '12.5%', width: '70%' }"
      class="is-change-group-anno"
    >
      <p>只有群主及管理员可以编辑群公告</p>
      <p @click="closeRouterWriterGroupAnno">知道了</p>
    </van-popup>

  </div>
</template>


<script>
import WebIM from "../../utils/WebIM";
import { mapState } from "vuex";
import { Dialog } from 'vant';
export default {
  data() {
    return {
      // 群成员数据
      userMessage: [],
      checked1: false,
      checked2: true,
      checked3: true,
      //   群聊id
      groupId: "",
      //   群聊名称
      groupName: "",
      //   群聊公告:
      groupAnnouncement: "",
      //   群主
      groupOwner: "",
      isRouterWriterGroupAnno: false,
      // 用户昵称
      userName: "",
      // 当前用户是否为当前群聊的群主
      isGroupOnwer: false,
      // 是否显示解散群聊的组件
      isDismissGroup: false,
      ownerId:"",
    };
  },
  computed: {
    ...mapState({
      newCreateGroupId: (state) => state.home.newCreateGroupId,
      cacheClickGroupId: (state) => state.home.cacheClickGroupId,
      userId: (state) => state.user.userId,
    }),
  },
  created() {
    this.getGroupId();
  },
  watch: {
    userId() {
      this.getGroupId();
    },
  },
  methods: {
    getGroupId() {
      if (this.userId !== "") {
        WebIM.conn.fetchUserInfoById(this.userId, "nickname").then((res) => {
          this.userName = res.data[this.userId].nickname;
        });

        if (this.newCreateGroupId !== "") {
          this.groupId = this.newCreateGroupId;
        } else if (
          this.newCreateGroupId == "" &&
          this.cacheClickGroupId !== ""
        ) {
          this.groupId = this.cacheClickGroupId;
        } else if (
          this.newCreateGroupId == "" &&
          this.cacheClickGroupId == ""
        ) {
          this.groupId = sessionStorage.getItem("groupInfoCacheSave");
        }
        this.getGroupMenbers();
        this.getGroupInfo();
        this.getGroupAnnouncement();
      }
    },
    getGroupMenbers() {
      let option = {
        pageNum: 1,
        pageSize: 200,
        groupId: this.groupId,
      };
      //获取群成员列表
      WebIM.conn.listGroupMembers(option).then((res) => {
        let newArr = res.data;
        newArr.map((item, index) => {
          //群主
           if(item.owner) {
              this.ownerId = item.owner
            }
          // 获取群成员信息
          WebIM.conn
            .fetchUserInfoById(item.owner ?? item.member)
            .then((res) => {
              //昵称
              let name = res.data[item.owner ?? item.member].nickname;
              //头像
              let picUrl =
                res.data[item.owner ?? item.member].avatarurl ??
                require("../../assets/image/addressImg/head.webp");
              let obj = {
                nickname: name,
                pic: picUrl,
              };
              this.userMessage.push(obj);
            });
        });
      });
    },

    // 获取群聊的相关信息
    getGroupInfo() {
      let option = {
        groupId: this.groupId,
      };
      WebIM.conn.getGroupInfo(option).then((res) => {
        this.groupName = res.data[0].name;
        this.groupOwner = res.data[0].owner;
        if (this.userId == this.groupOwner) {
          this.isGroupOnwer = true;
        } else {
          this.isGroupOnwer = false;
        }
      });
    },
    // 跳转至修改群聊名称的页面
    changeGroupName() {
      this.$router.push({
        path: "/changegroupname",
      });
    },

    // 跳转到群公告的地方
    getGroupAnnouncement() {
      let option = {
        groupId: this.groupId,
      };
      WebIM.conn.fetchGroupAnnouncement(option).then((res) => {
        this.groupAnnouncement = res.data.announcement;
      });
    },

    changeGroupAnnouncement() {
      if (this.groupOwner == this.userId) {
        this.$router.push({
          path: "/changegroupannouncement",
        });
      } else {
        this.isRouterWriterGroupAnno = true;
      }
    },
    closeRouterWriterGroupAnno() {
      this.isRouterWriterGroupAnno = false;
    },

    isToSetDismissGroup() {
      this.isDismissGroup = true;
    },
    cencelDisMissGroup() {
      this.isDismissGroup = false;
    },
    disMissGroup() {
      let option = {
        groupId: this.groupId,
      };
      WebIM.conn.destroyGroup(option).then((res) => {
        this.$toast.success("解散该群聊")
        this.isDismissGroup = false;
        this.$router.push({
          path:"/"
        })
      });
    },
    // 退出群聊
    bowOut(){
       
     Dialog.confirm({
         title: '确认退出群聊吗',
         message: '',
      })
    .then(() => {
      console.log(123);
      let option = {
         groupId:this.groupId
      }
      WebIM.conn.leaveGroup(option).then( res => {
        
          console.log(res)
          this.$toast('退出成功')
          this.$router.push('/')
      })



   })
    .catch(() => {
      // on cancel
      
    });
    }
  },
};
</script>


<style lang="scss" scoped>
.group-message {
  position: relative;
  color: #fff;
  width: 100vw;
  //    height: 200vh;
  z-index: 999;
  color: rgb(164, 164, 164);
  background-color: var(--theme-background-black) !important;
  .title {
    display: flex;
    align-items: center;
    height: 45px;
    padding: 5px;
    margin-bottom: 10px;
    background-color: rgb(67 67 67);
    h2 {
      flex: 1;
      text-align: center;
    }
  }

  .user {
    height: 200px;
    background: var(--theme-cell-black);
    border-radius: 10px 10px 0 0;
    padding: 5px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    .user-item {
      width: 60px;
      height: 60px;
      text-align: center;
      margin-right: 15px;
      .log {
        width: 50px;
        height: 50px;
        margin: auto;
        img {
          width: 100%;
        }
      }
      p {
        margin-top: 5px;
        font-size: 12px;
      }
    }
    .add,
    .del {
      width: 50px;
      height: 50px;
      border: 2px dashed rgba(245, 243, 243, 0.4);
      border-radius: 10px;
      text-align: center;
      line-height: 50px;
      font-size: 30px;
    }
  }
  .group-dec {
    margin-top: 10px;
    .group-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      // height: 40px;
      background: var(--theme-cell-black);
      padding: 10px;
      border-bottom: 1px solid rgba(204, 203, 203, 0.1);
      &:nth-child(6) {
        margin-top: 10px;
        margin-bottom: 10px;
      }
      .anno-info {
        width: 100%;
        .title {
          width: 100%;
          height: 25px;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          margin: 0;
          padding: 0;
          background: var(--theme-cell-black);
        }
        p {
          color: #474545;
         
        }
      }
    }
    .is-dismiss-group {
      width: 100%;
      height: 45px;
      line-height: 45px;
      text-align: center;
      background: var(--theme-cell-black);
      
      button {
        width: 100%;
        border: 0px solid #000;
        outline: 0px solid #000;
        color: red;
        background: var(--theme-cell-black);
      }
    }
  }
  .dismiss-group-popup {
    height: 100%;
    background: var(--theme-cell-black);
    p {
      height: 50% !important;
      width: 100%;
      text-align: center;
      &:not(:last-child) {
        padding: 15px;
        border-bottom: 1px solid #292929;
      }
      button {
        background: var(--theme-cell-black);
        width: 50%;
        border: 0px solid #000;
        outline: 0px;
        padding: 15px; 
        &:not(:last-child) {
          border-right: 1px solid #292929;
        }
      }
    }
  }
  .is-change-group-anno {
    text-align: center;
    background: var(--theme-cell-black);
    
    p {
      padding: 10px 15px;
      background: var(--theme-cell-black);
      &:not(:last-child) {
        border-bottom: 1px solid #e8e6e6;
      }
      &:last-child {
        color: #9999e2;
      }
    }
  }
  .bow-out {
     width: 100%;
     height: 40px;
     background: var(--theme-cell-black);
     color: red;
     text-align: center;
     line-height: 40px;
     margin-top: 10px;

  }
}
</style>